.App {
  text-align: center;
  overflow: auto;
  background-size: cover;
  background-color: #182130;
  width: 100vw;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header {
  /* font-size: 20px; */
  margin-right: 40px;
  margin-left: 40px;
  @media (orientation:portrait) {
    margin-right: 25px;
    margin-left: 25px;
  }
}

h1 {
  color: #49bef9;
  font-size: 70px;
  text-align: center;
  letter-spacing: 15px;
  -webkit-text-stroke: 17px #49bef95f;
  -webkit-text-fill-color: #49bff9;
  text-shadow: 0px 0px 7px #75c9f3;
  @media (orientation:portrait) {
    font-size: 30px;
    letter-spacing: 5px;
    -webkit-text-stroke: 7px #49bef95f;
    -webkit-text-fill-color: #49bff9;
    text-shadow: 0px 0px 7px #75c9f3;
  }
}

h2 {
  color: #49bef9;
  font-size: 60px;
  text-align: center;
  letter-spacing: 5px;
  margin-top: 15px;
  margin-bottom: -20px;
  -webkit-text-stroke: 7px #49bef95f;
  -webkit-text-fill-color: #49bff9;
  text-shadow: 0px 0px 2px #75c9f3;
  @media (orientation:portrait) {
    font-size: 25px;
  }
}

h3 {
  color: #49bef9;
  font-size: 40px;
  text-align: center;
  letter-spacing: 5px;
  /* margin-top: 15px; */
  /* margin-bottom: 20px; */
  -webkit-text-stroke: 7px #49bef95f;
  -webkit-text-fill-color: #49bff9;
  text-shadow: 0px 0px 2px #75c9f3;
  @media (orientation:portrait) {
    font-size: 20px;
  }
}

h4 {
  color: #49bef9;
  font-size: 27px;
  text-align: center;
  letter-spacing: 5px;
  margin-top: 15px;
  margin-bottom: 20px;
  -webkit-text-stroke: 7px #49bef95f;
  -webkit-text-fill-color: #49bff9;
  text-shadow: 0px 0px 2px #75c9f3;
  @media (orientation:portrait) {
    font-size: 15px;
  }
}

p {
  color: #49bef9;
  font-size: 23px;
  text-align: left;
  @media (orientation:portrait) {
    font-size: 17px;
  }
}

